/*-----------------------------------------------
|   Headers
-----------------------------------------------*/


h5, .h5 {
  margin-bottom: map_get($spacers, 1);

}



/*-----------------------------------------------
|   Drop cap
-----------------------------------------------*/
.dropcap::first-letter {
  font-size: 3em;
  float: left;
  line-height: .92;
  margin-right: 0.375rem;
}


/*-----------------------------------------------
|   Letter Spacing
-----------------------------------------------*/
.ls {
  letter-spacing: 0.120rem;
}

.ls-1 {
  letter-spacing: 0.1776rem;
}

.ls-2 {
  letter-spacing: 0.8439625rem;
}

.lh-2 {
  line-height: 1.2;
}

/*-----------------------------------------------
|   List
-----------------------------------------------*/
.bullet-inside {
  list-style-position: inside;
}

// Style Check mark
.style-check li {
  position: relative;
  list-style-type: none;
  padding-left: .25rem;

  &:before {
    content: url(data:image/svg+xml;base64,);
    padding-right: 0.1875rem;
    position: absolute;
    top: 0.0625rem;
    left: -1rem;
    background-repeat: no-repeat;
    width: 1rem;
  }
}

/*-----------------------------------------------
|   Blockquote
-----------------------------------------------*/
.blockquote-content {
  font-style: italic;
  position: relative;

  &:before {
    position: absolute;
    left: -3rem;
    top: -0.5rem;
    line-height: 1;
    content: quote('\201C');
    display: inline-block;
    color: $gray-300;
    font-size: map_get($font-sizes, 6);
  }
}

.blockquote-footer {
  &::before {
    transform: translateY(0.125rem);
    font-weight: $font-weight-normal;
    display: inline-block;
  }
}


